import React, { Component } from 'react';
import EpicMenu from './EpicMenu';
import './Portfoilo.css';
import logo from './signature.svg';
import curated from './curated.JPG';
import freshview from './ourfreshview.JPG';
import brad from './bradciechanowski.JPG';


class Portfolio extends Component {
  state = {
    experience: [
      {id:1,company:'Curated Trades',title: 'Full-Stack Developer', desc: "Lorem ipsum dolor ", img:curated},
      {id:2,company:'OurFreshView',title:'Full-Stack Developer', desc: "Lorem ipsum dolor", img: freshview},
      {id:3,company:'Bradciechanowki.com',title:'Full-Stack Developer', desc: "Lorem ipsum dolor ",img: brad}
    ]
  };
    render() {
      let links = [
        { label: 'Home', link: 'Home' },
        { label: 'Portfolio', link: 'Portfolio', active: true  },
        { label: 'Products', link: 'Products' },
        { label: 'Contact', link: 'Contact'},
      ];

      let experienceList = this.state.experience.map(exper=>{
        return(
          <div className="flip-card" key={exper.id}>
              <div className="flip-card-inner">
                  <div className="flip-card-front">
                  <img src={exper.img} alt={exper.company} style={{width:'700px', height:'400px'}}/>
                  </div>
                  <div className="flip-card-back">
                  <h1>{exper.company}</h1> 
                  <p>{exper.title}</p> 
                  <p>{exper.desc}</p>
                  </div>
              </div>
          </div>
          
        )
      })
  
      const Aux = (props) => {
        return props.children;
      };
  
      return (
        <Aux>
        <div className="container center">
          <EpicMenu links={links} logo={logo}/>
        </div>
        <div className="portlist">
          {experienceList}
        </div>
        </Aux>
      );
    }
  }
  
  
  export default Portfolio;
  